.footer {
    display: flex;
    width: 1280px;
    flex-direction: column;
    padding-top: 66px;
}

.footer_top {
    display: flex;
    width: 1250px;
    height: 56px;
    margin-top: 96px;
    background-color: #121212;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
}

.footer_top p{
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: #FFFFFF;
}

.join {
    display: flex;
    align-items: center;
}

.footer_top input {
    display: flex;
    width: 400px;
    height: 32px;
    background: #FFFFFF;
    opacity: 0.2;
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 18px;
    margin-right: 12px;
    color: #FFFFFF;
    opacity: 0.4;
}

.footer_top button {
    display: flex;
    width: 108px;
    height: 32px;
    background-color: #121212;
    border: 1px solid #FFFFFF;
    box-sizing: border-box;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    align-items: center;
    justify-content: center;
    text-align: center;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: #FFFFFF;
    opacity: 0.9;
}

.social_bot {
    display: flex;
    border: 1px solid red;
    width: 108px;
    height: 18px;
    justify-content: space-between;
} 

.f_1 {
    width: 18px;
    height: 18px;
    background-image: url("../../components/facebook_f.svg");
    border-radius: none;
}

.f_2 {
    width: 18px;
    height: 18px;
    background-image: url("../../components/twitter_f.svg");
}

.f_3 {
    width: 18px;
    height: 18px;
    background-image: url("../../components/instagram_f.svg");
}

.f_4 {
    width: 18px;
    height: 18px;
    background-image: url("../../components/pinterest_f.svg");
}

.footer_main{
    display: flex;
    width: 1250px;
    height: 237px;
    padding: 0 15px;
    align-items: center;
    justify-content: space-between;
    background: #FFFFFF;
}

.categories {
    display: flex;
    align-items: flex-start;
    width: 255px;
    height: 157px;
    flex-direction: column;
    border: 1px solid red;
}

.categories h3 {
    margin-bottom: 15px;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 13px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: #121212;
}

.categories a {
    text-decoration: none;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 34px;
    color: #121212;
    opacity: 0.8;
}

.categories p {
    padding-left: 26px;
}

.loc {
    display: flex;
    align-items: center;
    margin-bottom: 18px;
    width: 225px;
    height: 18px;
    background-image: url("../../components/location_f.svg");
    background-position: left;
    background-repeat: no-repeat;
}

.num {
    display: flex;
    align-items: center;
    margin-bottom: 18px;
    width: 225px;
    height: 18px;
    background-image: url("../../components/phone_f.svg");
    background-position: left;
    background-repeat: no-repeat;
    background-size: contain;
}

.times {
    display: flex;
    align-items: center;
    margin-bottom: 18px;
    width: 225px;
    height: 18px;
    background-image: url("../../components/clock_f.svg");
    background-position: left;
    background-repeat: no-repeat;
    background-size: contain;
}

.mail {
    display: flex;
    align-items: center;
    width: 225px;
    height: 18px;
    background-image: url("../../components/mail_f.svg");
    background-position: left;
    background-repeat: no-repeat;
}

.footer_bot {
    display: flex;
    width: 1250px;
    height: 40px;
    background-color: #f8f8f8;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
}

.prava {
    display: flex;
    align-items: center;
}

.prava p {
    font-style: normal;
    font-weight: 500;
    font-size: 11px;
    line-height: 22px;
    color: #121212;
    opacity: 0.8;
}

.spons {
    display: flex;
    width: 381px;
    height: 16px;
    justify-content: space-between;
}

.spons_1 {
    width: 43px;
    background-image: url("../../components/Stripe.svg");
}
.spons_2 {
    width: 31px;
    background-image: url("../../components/AES.svg");
}
.spons_3 {
    width: 61px;
    background-image: url("../../components/paypal.svg");
}
.spons_4 {
    width: 52px;
    background-image: url("../../components/visa.svg");
}
.spons_5 {
    width: 27px;
    background-image: url("../../components/mastercard.svg");
}
.spons_6 {
    width: 50px;
    background-image: url("../../components/discover.svg");
}
.spons_7 {
    width: 47px;
    background-image: url("../../components/american-express.svg");
}

.clever a {
    font-style: normal;
    font-weight: 500;
    font-size: 11px;
    line-height: 22px;
    text-align: right;
    text-decoration-line: underline;
    color: #121212;
    opacity: 0.8;
}